/* You can add global styles to this file, and also import other style files */
$title-font: 'Oleo Script', cursive;
$non-title-font: 'Raleway', sans-serif;
*{
    padding: 0;
    margin: 0;
    list-style: none;
    text-decoration: none;
    color:white;
    font-family: $non-title-font;
}
body{
    background-color: #181818;
}
h1,h2{
    font-family: $title-font;
}
h1{
    font-size: 35px;
}
h2{
    font-size: 35px;
}
.title{
    width: 100%;
    border: 2px solid #393939;
    text-align: center;
    background-color: #202020;
    margin-bottom: 20px;
}
.common-container{
    margin: 10px auto;
    border: 2px solid #393939;
    background-color: #202020;
    width: 90%;
    padding:10px;
    &.join-container{
        width: 30%;
    }
}
input[type="text"],input[type="password"],input[type="email"]{
    padding:5px;
    border: 2px solid #393939;
    background-color: #181818;
    color:white;
    font-family: $non-title-font;
    width: 240px;
}
label{
    display:block;
    margin-top:20px;
}
button,input[type="submit"],input[type="button"]{
    display: block;
    padding:5px;
    margin-top: 20px;
    background:#181818;
    border: 2px solid #393939;
    box-shadow: 0px 0px 6px rgba(255, 255, 255, 0.507);
    cursor:pointer;
    &:disabled{
        color:#595959;
        box-shadow: none;
        cursor:auto;
    }
    transition: 300ms all;
}
input[type="file"]{
    width:100px;
    background:#181818;
    border: 2px solid #393939;
    font-family: $non-title-font;
}
.alert{
    display: block;
    color:#eb3434;
    font-size:12px;
    margin-top:3px;
}
